




















































import Vue from 'vue'
interface cardDecks {
  deck1: Array<string>
  deck2: Array<string>
  deck3: Array<string>
  deck4: Array<string>
  deck5: Array<string>
  deck6: Array<string>
  deck7: Array<string>
  deck8: Array<string>
}
export default Vue.extend({
  name: 'Solitaire',
  data () : {
    pokerCards: Array<string>
    cardDecks: cardDecks
    } {
    return {
      'pokerCards': [
        'spade-01',
        'spade-02',
        'spade-03',
        'spade-04',
        'spade-05',
        'spade-06',
        'spade-07',
        'spade-08',
        'spade-09',
        'spade-10',
        'spade-11',
        'spade-12',
        'spade-13',
        'heart-01',
        'heart-02',
        'heart-03',
        'heart-04',
        'heart-05',
        'heart-06',
        'heart-07',
        'heart-08',
        'heart-09',
        'heart-10',
        'heart-11',
        'heart-12',
        'heart-13',
        'diamond-01',
        'diamond-02',
        'diamond-03',
        'diamond-04',
        'diamond-05',
        'diamond-06',
        'diamond-07',
        'diamond-08',
        'diamond-09',
        'diamond-10',
        'diamond-11',
        'diamond-12',
        'diamond-13',
        'club-01',
        'club-02',
        'club-03',
        'club-04',
        'club-05',
        'club-06',
        'club-07',
        'club-08',
        'club-09',
        'club-10',
        'club-11',
        'club-12',
        'club-13'
      ],
      'cardDecks': {
        'deck1': [],
        'deck2': [],
        'deck3': [],
        'deck4': [],
        'deck5': [],
        'deck6': [],
        'deck7': [],
        'deck8': []
      }
    }
  },

  computed: {
    shuffledDeck () :cardDecks {
      return this.cardDecks
    }
  },

  mounted () {
    this.distributeCards()
  },

  methods: {
    distributeCards () {
      let shuffledDeck = this.shuffleDeck()
      while (shuffledDeck.length > 0) {
        Object.keys(this.cardDecks).forEach((deckName:string) => {
          (this.cardDecks as any)[deckName].push(shuffledDeck.pop())
        })
      }
    },

    shuffleDeck ():Array<string> {
      let shuffledDeck = [ ...this.pokerCards ]
      for (let i = this.pokerCards.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1))
        let temp = shuffledDeck[i]
        shuffledDeck[i] = shuffledDeck[j]
        shuffledDeck[j] = temp
      }
      return shuffledDeck
    },

    getCardImg (card:string):string {
      return card ? require(`../assets/${card}.png`) : null
    },

    getTop (idx:number):string {
      return `${idx * 35}px`
    }
  }
})
